@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";

// 斜槓人生 
.ctc-bg{
  transition: background-color 0.5s ease;
}
.cathay-carosel{
  &.#{$prefix}-layout-04{
    .slick-slider{
      // width: 960px;
      // margin-left: auto;
      // margin-right: auto;
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        width: 960px;
        margin-left: auto;
        margin-right: auto;
      }
    }
    .slick-list{
      padding:0;
    }
    .slick-track{
      padding-top: 25px;
      padding-bottom: 15px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        // margin:25px 0 50px;
      }

    }
    .slick-slide{
      @media (min-width: map-get($grid-breakpoints, 'md')){
        // margin:0 6px;
        overflow: hidden;
        padding: 0px 60px 10px;
        >.cs-link{
          display: flex;
          flex-wrap: wrap;
        }
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        padding: 0px 40px;
      }

      &:hover,&:active{
        >.cs-link{
          // opacity:1;
        }
        .btn{
          color: #fff;
          background-color: #d5780a;
          border-color: #d5780a;
        }
      }
    }
    .#{$prefix}link{
      display: block;
    }
    .#{$prefix}title{
      font-size:30px;
      font-weight: 300;
      line-height: 1;
      color:#00a94f;
      letter-spacing: 2px;
      text-align: center;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        padding-left:30px;
        font-size:30px;
        text-align: left;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        font-size:40px;
      }
    }
    .#{$prefix}subtitle{
      display: none;
    }
    .#{$prefix}content{
      font-size:16px;
      line-height: 1.5;
      color:#666666;
      font-weight: 400;
      letter-spacing: 1px;
      width: 65%;
      margin-left:auto;
      margin-right:auto;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        width: 100%;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }


    .#{$prefix}subtitle + .#{$prefix}content{
      margin-top: 10px;
    }


    .#{$prefix}title + .#{$prefix}content{
      margin-top: 10px;
    }

    .#{$prefix}txt-wrap{
      display: block;
      align-self: center;
      order:10;
      display: block;
      padding:0px;
      position: relative;
      width: 100%;
      margin:0 auto;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        flex-basis: 240px;
        width: auto;
        margin:0 10px 0 0;
      }
    }


    p{
      font-size:16px;
      font-weight: 400;
    }
    ul{
      list-style: disc;
      padding-left: 30px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        padding-left: 30px;
      }
    }
    li{
      font-size:16px;
      font-weight: 400;
    }
    .#{$prefix}img-wrap{
      order:20;
      $ratio:0.6;
      width: round($ratio * 600px);
      height: round($ratio * 350px);
      background-size:cover;
      overflow: hidden;
      background-repeat: no-repeat;
      background-position: center top;
      vertical-align: middle;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 25px;
      img{
        visibility: hidden;
      }

      
      @media (min-width: map-get($grid-breakpoints, 'md')){
        $ratio:.65;
        width: round($ratio * 600px);
        height: round($ratio * 350px);
        flex-basis: round($ratio * 600px);
        margin:0;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        $ratio:.95;
        width: round($ratio * 600px);
        height: round($ratio * 350px);
        flex-basis: round($ratio * 600px);
      }

    }

    .#{$prefix}more{
      display: block;
      text-align: center;
      padding-top: 15px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        // margin-top: 10px;
        text-align: left;
      }
    }
    .btn{
      // font-size:16px;
      // height: 52px;
      // line-height: 50px;
      // padding: 0px 40px;
      &:hover{
        color:#fff;
      }
    }

    .slick-prev,.slick-next {
      top: calc(50% + 35px);
      @media (min-width: map-get($grid-breakpoints, 'md')){
        top: calc(50% - 20px);
      }
    }
    
    .slick-prev {
      @media (min-width: map-get($grid-breakpoints, 'md')){
        left: 10px;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        left: -15px;
      }
      @media (min-width: map-get($grid-breakpoints, 'xl')){
        left: -8%;
      }
    }
    .slick-next {
      @media (min-width: map-get($grid-breakpoints, 'md')){
        right: 10px;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        right: -15px;
      }
      @media (min-width: map-get($grid-breakpoints, 'xl')){
        right: -8%;
      }
    }
  }
}